import Link from "next/link";

type Props = { href?: string; label: string; variant?: "primary"|"secondary"|"disabled"; onClick?():void };
export default function CTAButton({ href, label, variant="primary", onClick }: Props){
  const base = "h-14 rounded-md text-[17px] font-bold flex items-center justify-center";
  const styles = {
    primary: "bg-primary text-white",
    secondary: "bg-secondary text-primary border border-divider",
    disabled: "bg-secondary text-disabled border border-divider pointer-events-none",
  }[variant];

  const btn = <div className={`${base} ${styles}`}>{label}</div>;
  return href ? <Link href={href}>{btn}</Link> : <button onClick={onClick}>{btn}</button>;
}
